.MenuControl{
    width:100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    background-color: #546e32;
    margin: auto;
    padding: 10px 0;
    -webkit-box-shadow: 0 150px 150px rgb(97, 117, 78);
            box-shadow: 0 150px 150px rgb(97, 117, 78);
}  

.orderButton{
    height: 50px;
    width: 90px;
    background-color: rgb(55, 247, 80);
    outline: none;
    border: 1px solid #f78af7;
    color: darkred;
    padding: 10px,20px;
    font-family:inherit;
    -webkit-box-shadow: 4px 4px 4px darkred;
            box-shadow: 4px 4px 4px darkred;
    font-size: 1.2em ;
    cursor: pointer;
}

.orderButton:hvor, .orderButton:active{
    background: rgb(6, 141, 118);
    border: 1px solid #2a6486;
    color: darkred; 
}

.orderButton:disabled{
    background-color: rgb(252, 224, 135);
    color: brown;
    border: 1px solid #f78af7;
    cursor: not-allowed;
}

.orderButton:not(:disabled){
   -webkit-animation: enable 0.2s linear;
           animation: enable 0.2s linear;
}

@-webkit-keyframes enable{
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60%{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable{
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60%{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



