.OptionControl{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin: 3px 0;
    padding: 5px 0;
    height: 100%;
}

.OptionControl button{
    display: block;
    
    font: inherit;
    width: 80px;
    padding: 5px 2px;
    margin: 2px 5px;
    border: 1px solid brown;
    cursor: pointer;
    outline: none;
   background-color: rgb(241, 157, 202);
}

.OptionControl button:disabled{
    background-color: chocolate;
    border: 1px solid rgb(46, 22, 5);
    color: cornsilk;
    cursor: default;
}

.OptionControl button:hover:disabled{
    background-color: darkgoldenrod;
    color: darkgreen;
    cursor: not-allowed;
}

.options{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 10px ;
    font-weight: bold;
    width: 80px;
   
}

.OptionControl.Add{
    background-color: blueviolet;
    color: chartreuse;
}

.OptionControl.Remove{
    background-color: blueviolet;
    color: chartreuse;
}

.OptionControl.Add:hover, .OptionControl.Add:active{
    background-color: darkorange;
    color: darkred;
}

.OptionControl.Remove:hover, .OptionControl.Remove:active{
    background-color: darkorange;
    color: darkred;
}


