.modal{
    width: 50%;
    position: fixed;  
    z-index: 400;
    background-color: cyan;
    color: darkblue;
    border: 2px solid darkblue;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 20px;
    left: 20%;
    top: 35%;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}


