.Toolbar{
    width: 100%;
    height: 50px;
    position: fixed;
    background: rgb(236, 114, 216);
    color: seashell;
    left: 0;
    top: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav{
    height: 100%;
}

.Logo{
    height: 60%;
}

@media(max-width: 499px) {
    .DesktopOnly {
        display: none;
    }
}


